@import "simple-grid";

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #fff;
  color: #fff;
  font-size: 18px;
}

ul, li {
  display: block;
  list-style-type: none;
}

h1, p {
  margin: 0 0 1em 0;
  color: #fff;
}

.title {
  font-size: 64px;
  line-height: 32px;
  color: #f4f4f4;
  letter-spacing: 1px;
  font-weight: 300;
}

.title b{
  color: #f4f4f4;
}

.btn {
  width: 140px;
  height: 42px;
  display: block;
  background-color: white;
  color: #2ecc71;
  text-decoration: none;
  text-align: center;
  padding: 6px 12px;
  border-radius: 20px;
}

.wrapper {
  display: flex;
  min-height: 100%;
  overflow: hidden;
  flex-direction: column;
}

.header {
  background: url('./assets/images/header-background.png') no-repeat center top / cover;
}

header a {
  color: #fff;
}

.header__logo {
  //height: 38px;
  //width: 130px;
}

.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 28px 0;
}

.header__nav {
  display: flex;
  justify-content: flex-end;
}

.menu__list {
  display: flex;
}

.menu__list li:not(:last-child)  {
  margin-right: 60px;
}

.menu__list li a {
  text-transform: uppercase;
  text-transform: capitalize;
  text-decoration: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header_content__row {
  padding-top: 50px;
}

.header__content {
  padding-top: 120px;
}

.header__title {
  padding-top: 34px;
}

.header__text {
  padding-top: 42px;
}

.download__list {
  display: flex;
  padding-top: 36px;
}

.download__item {
  width: 130px;
  height: 46px;
  margin-right: 30px;
}

.appstore {
  background: url("./assets/images/Appstore.png");
}

.googleplay {
  background: url("./assets/images/Google_Play.png");
}

.header__sidebar {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0 !important;
}

.test {
  background: url("./assets/images/iPhone_5C_Mock_Up_Features.png");
}